@import url("https://fonts.googleapis.com/css2?family=Tenor+Sans&display=swap");
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

img {
  display: block;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: 400;
}

.container {
  max-width: 1190px;
  margin: 0 auto;
  padding: 0 15px;
}

.container-fluid {
  max-width: 1920px;
  margin: 0 auto;
}

body {
  font-family: "Tenor Sans", sans-serif;
  font-weight: 400;
}

.btn {
  font-family: "Tenor Sans", sans-serif;
  background-color: #f3d5ae;
  border-radius: 4px;
  padding: 14px 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  border: none;
  cursor: pointer;
  transition: 0.2s;
}
.btn:hover {
  background-color: #f6c483;
}

.h2 {
  font-size: 32px;
  font-weight: 400;
  line-height: 130%;
  letter-spacing: 0.02em;
  text-transform: uppercase;
  color: #262626;
}

.breadcrumb {
  margin-top: 32px;
}
@media (max-width: 768px) {
  .breadcrumb {
    display: none;
  }
}
.breadcrumb__list {
  display: flex;
}
.breadcrumb__item {
  font-size: 16px;
  line-height: 150%;
  color: #bfbfbf;
}
.breadcrumb__item + .breadcrumb__item::before {
  content: "/";
  padding: 0 6px 0 10px;
  color: #262626;
}
.breadcrumb__link {
  color: #262626;
  transition: 0.8s;
  position: relative;
}
.breadcrumb__link::after {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  width: 0%;
  height: 1px;
  background-color: #262626;
  transition: 0.2s;
}
.breadcrumb__link:hover::after {
  width: 100%;
}

.header {
  background-color: #7f713e;
}
.header__nav {
  display: flex;
  align-items: center;
  padding: 16px 0px;
}
@media (max-width: 1100px) {
  .header__nav {
    justify-content: space-between;
    height: 60px;
  }
}
.header__logo {
  max-width: 143px;
}
@media (max-width: 1100px) {
  .header__logo {
    display: none;
  }
}
.header__logo-mobile {
  max-width: 33px;
  display: none;
}
@media (max-width: 1100px) {
  .header__logo-mobile {
    display: block;
  }
}
.header__logo-img {
  max-width: 100%;
}
.header__menu {
  display: flex;
  gap: 8px;
  margin: 0 auto;
}
@media (max-width: 1100px) {
  .header__menu {
    display: none;
  }
}
.header__menu-link, .header__phone {
  font-size: 14px;
  line-height: 16px;
  text-transform: uppercase;
  color: #fff;
}
.header__menu-link {
  padding: 6px 10px;
  transition: 0.2s;
}
.header__menu-link:hover {
  border: 1px solid #fff;
  border-radius: 100px;
}
.header__phone:hover {
  border-bottom: 1px solid #fff;
}
.header__social {
  display: flex;
  gap: 16px;
  margin-left: 32px;
}
@media (max-width: 1100px) {
  .header__social {
    display: none;
  }
}
.header__social-link {
  transition: 0.2s;
}
.header__social-link:hover {
  transform: translateY(-3px);
}
.header__burger {
  display: none;
  width: 24px;
  height: 21px;
  background-image: url(../img/burger.svg);
  background-repeat: no-repeat;
  transition: 0.2s;
}
@media (max-width: 1100px) {
  .header__burger {
    display: block;
  }
}
.header__burger.close {
  background-image: url(../img/burger-close.svg);
}

.header-main {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  background-color: transparent;
  transition: background-color 0.4s;
}

@media (max-width: 1100px) {
  .header-main.show {
    background-color: #7f713e;
  }
}

.mobile-menu {
  position: fixed;
  top: 60px;
  left: 0;
  width: 100%;
  padding-bottom: 16px;
  transform: translateX(120%);
  background-color: #7f713e;
  transition: 0.4s;
  z-index: 1;
}
.mobile-menu__item {
  text-align: center;
  margin-top: 16px;
}
.mobile-menu__link {
  font-size: 14px;
  line-height: 16px;
  text-transform: uppercase;
  color: #fff;
}
.mobile-menu__social {
  display: flex;
  justify-content: center;
  gap: 16px;
  margin-top: 24px;
}
.mobile-menu.show {
  transform: translateX(0%);
}

