.running-line {
  background-color: #7f713e;
  padding: 24px 0;
  overflow: hidden;
  display: flex;
  margin-top: 120px;
}
@media (max-width: 770px) {
  .running-line {
    display: none;
  }
}
.running-line:hover .running-line__text {
  animation-play-state: paused;
}
.running-line__text {
  font-size: 24px;
  line-height: 120%;
  text-transform: uppercase;
  color: #fff;
  display: flex;
  gap: 40px;
  padding-right: 40px;
  white-space: nowrap;
  transform: translateX(0%);
  animation: running-text 15s infinite linear;
}
@keyframes running-text {
  0% {
    transform: translateX(0%);
  }
  100% {
    transform: translateX(-100%);
  }
}
