/* modal===================== */
.modal {
  transform: scale(0);
  pointer-events: none;
  transition: 0.2s;
}

.modal.open {
  transform: scale(1);
  pointer-events: all;
}

.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  height: 100vh;
  width: 100%;
  background: rgba(0, 0, 0, 0.4);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1;
}

.modal__body {
  max-width: 560px;
  height: 100%;
  background-color: #fff;
  display: flex;
  flex-direction: column;
  position: relative;
  opacity: 0;
  border-radius: 4px;
  overflow: hidden;
}

